* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: sans-serif;
}

header {
  min-height: 10vh;
  background-color: #1895b1;
  border-bottom: 1px solid #117e97;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.is-hidden {
  visibility: hidden;
}

.search-form {
  padding: 2rem;
  display: flex;
}

.search-form input {
  width: 100%;
  border: 3px solid #117e97;
  border-radius: 10px 0 0 10px;
  padding: .5rem;
  font-size: 1rem;
}

.search-form input:focus {
  outline: none;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background: #117e97;
  border: none;
  border-radius: 0 10px 10px 0;
  padding: 1rem;
  font-size: 1rem;
}

.info {
  color: gray;
  justify-content: space-between;
  align-items: center;
  padding: .5rem 0;
  font-size: 14px;
  display: flex;
}

.info a {
  color: gray;
}

.gallery {
  width: 100%;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 4rem 2rem;
  margin: auto;
  padding: 2rem;
  display: grid;
}

.gallery img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.load-more {
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  height: 40px;
  text-align: center;
  background-color: #117e97;
  border: 2px solid #117e97;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin: 20px auto;
  padding: 0 17px;
  font-size: 16px;
  font-weight: 600;
  text-decoration: none;
  display: flex;
}

.load-more:hover {
  fill: #a3c7d6;
  background-color: #117e97;
  border-color: #117e97;
}

#myBtn {
  z-index: 99;
  cursor: pointer;
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 5px;
  outline: none;
  padding: 10px 10px 5px;
  font-size: 30px;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
}

#myBtn:hover {
  background-color: #a3c7d6;
}

